import React, { useState, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import IconButton from "@mui/material/IconButton";
import { loadIcon } from "./LoadIcon";

const dispatchedEvents = [];

const SrsMuiIconButton = function (props, ref) {
  const [IconComp, setIconComp] = useState(null);

  useEffect(() => {
    loadIcon(props.icon || "Info", setIconComp);
  }, []);

  useEffect(() => {
    loadIcon(props.icon || "Info", setIconComp);
  }, [props.icon]);

  return (
    <IconButton {...props} theme={props.muiTheme}>
      <slot />
      {IconComp? <IconComp/>: null}
    </IconButton>
  );
};

const styles = `
  /* Custom styles for the MuiIconButton component */
`;

const props = [
  {
    name: "color",
    defaultValue: "default",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "edge",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "size",
    defaultValue: "medium",
    type: "string",
  },
  {
    name: "icon",
    defaultValue: "Add",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to IconButton if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiIconButton),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
